import React, { useEffect, useState } from 'react';
import { useAuth } from '../../AuthContext';
import { useNavigate } from 'react-router-dom'; // Импортируйте useNavigate
import './OpenDocument.css';
import { Link, useParams, useSearchParams } from 'react-router';
import DocViewer, { DocViewerRenderers } from 'react-doc-viewer';

const defaultSettings = {
    theme: 'light',
    colorScheme: 'orange',
    fontFamily: 'Roboto',
    fontSize: '16',
    language: 'ru'
};

// Загрузка настроек из localStorage
const loadSettings = () => {
    const settings = JSON.parse(localStorage.getItem('userSettings'));
    return settings ? settings : defaultSettings;
};
// Применение настроек
const applySettings = (settings) => {
    const main = document.querySelector('.main');

    main.classList.remove('light', 'dark');
    main.classList.add(settings.theme);

    main.classList.remove('orange', 'blue', 'gray');
    main.classList.add(settings.colorScheme);

    main.style.fontFamily = settings.fontFamily;
    main.style.fontSize = settings.fontSize + 'px';
};

const OpenDocument = () => {
    const [settings, setSettings] = useState(loadSettings());
    useEffect(() => {
        applySettings(settings);
    }, [settings]);

    const lang = settings.language;
    
    const { login } = useAuth();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [showError, setShowError] = useState(false); // Состояние для отображения ошибки
    const navigate = useNavigate(); // Получите объект navigate

    const [passwordVisible, setPasswordVisible] = useState(false);
    const [activeIconPassword, setActiveIconPassword] = useState(false);
    const togglePasswordVisibility = () => {
        setPasswordVisible(!passwordVisible);
    };
    const handleFocus = () => {
        setActiveIconPassword(true); // Устанавливаем активный класс при фокусировке
    };
    const handleBlur = () => {
        setActiveIconPassword(false); // Убираем активный класс при дефокусировке
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            await login(username, password);
            navigate('/home'); // Переход на страницу /home после успешного входа
        } catch (err) {
            // setError('Неверный логин и/или пароль'); // Устанавливаем сообщение об ошибке
            setError(lang === 'ru' ? 'Неверный логин и/или пароль' : 'Invalid username and/or password');
            setShowError(true);

            // Убираем ошибку через 3 секунды
            setTimeout(() => {
                setShowError(false);
                setError(''); // Сбрасываем сообщение об ошибке
            }, 3000);
        }
    };

    const { documentName } = useParams();

    const handleQuit = () => {
        navigate('/');
    }

    const [searchParams] = useSearchParams();
    const linkPage = searchParams.get('prevPage');;
    const nameProjectAllDoc = searchParams.get('nameProject')
    const linkAllDocs = `/${linkPage}${nameProjectAllDoc ? `/${nameProjectAllDoc}` : ''}`;

    return (
        <div className="open_doc-container">
            <div className="open_document-header">
                <Link to={linkAllDocs} id="open_doc-btn">
                    <img src={require('../../assets/icons/arrow.png')} alt="" />
                </Link>
                {/* <div className="open_doc-title"><span>Название документа</span></div> */}
                <div className="open_doc-title"><span>{decodeURIComponent(documentName)}</span></div>
                <div className="open_doc-btns">
                    <Link to={`/settings?prevPage=open_document`} className='open_doc-btn settings'>
                        <img src={require('../../assets/icons/settings.png')} alt="" />
                    </Link>
                    <div className="open_doc-btn quit" onClick={handleQuit}>
                        <img src={require('../../assets/icons/quit.png')} alt="" />
                    </div>
                </div>
            </div>
            <div className="open_document-main">
                <iframe src={require('../../assets/documents/example_doc.pdf')}></iframe>
            </div>
        </div>
    );
};

export default OpenDocument;