import React, { useEffect, useState } from 'react';
import { getProjects } from '../../api'; // Импортируйте функцию для получения проектов
import { useAuth } from '../../AuthContext'; // Импортируйте useAuth

const defaultSettings = {
    theme: 'light',
    colorScheme: 'orange',
    fontFamily: 'Roboto',
    fontSize: '16',
    language: 'ru',
    blurBg: 'noblur',
    invertTextHeader: 'no-inverted',
    panelVisible: 'visible'
};

// Загрузка настроек из localStorage
const loadSettings = () => {
    const settings = JSON.parse(localStorage.getItem('userSettings'));
    return settings ? settings : defaultSettings;
};
// Применение настроек
const applySettings = (settings) => {
    const main = document.querySelector('.main');

    main.classList.remove('light', 'dark');
    main.classList.add(settings.theme);

    main.classList.remove('orange', 'blue', 'gray');
    main.classList.add(settings.colorScheme);

    main.style.fontFamily = settings.fontFamily;
    main.style.fontSize = settings.fontSize + 'px';

    if (settings.invertTextHeader === 'inverted') {
        document.querySelector('.main_content-header-item-title').classList.add('invert-text');
    } else {
        document.querySelector('.main_content-header-item-title').classList.remove('invert-text');
    }
    if (settings.panelVisible === 'visible') {
        document.querySelector('.main_content-panel').classList.add('visible-panel');
        document.querySelector('.main_content-panel').classList.remove('novisible-panel');
    } else {
        document.querySelector('.main_content-panel').classList.add('novisible-panel');
        document.querySelector('.main_content-panel').classList.remove('visible-panel');
    }
};

const TreeNode = ({ node }) => {
  const [expanded, setExpanded] = useState(false);
  const hasChildren = node.children && node.children.length > 0;
  const className = hasChildren ? "sidebar-item-block group-list" : "sidebar-item-block";

  const toggle = () => {
    if (hasChildren) {
      setExpanded(!expanded);
    }
  };

  return (
    <li>
      <details>
        <summary>
          <div className={className}>
            <span className="styled-text">{node.shortname}</span>
            {hasChildren && <img src={require('../../assets/icons/chevron.png')} alt="" />} {/* Добавляем изображение только если есть вложенные группы */}
          </div>
        </summary>
        {hasChildren && (
          <ul style={{paddingLeft: '20px'}}>
            {node.children.map(child => (
              <TreeNode key={child.id} node={child} />
            ))}
          </ul>
        )}
      </details>
    </li>
  );
};

const ProjectTree = () => {
  const [settings, setSettings] = useState(loadSettings());
      useEffect(() => {
          applySettings(settings);
      }, [settings]);

  const lang = settings.language;

  const { token } = useAuth(); // Получаем токен из контекста
  const [treeData, setTreeData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchProjects = async () => {
      if (!token) {
        // setError('Not authenticated. Please log in.');
        setError(`${lang === 'ru' ? 'Не авторизован. Пожалуйста, войдите в систему.' : 'Not authenticated. Please log in.'}`);
        setLoading(false);
        return;
      }

      try {
        const data = await getProjects(token);
        if (data) {
          setTreeData(data);
        } else {
          // setError('Failed to load data.');
          setError(`${lang === 'ru' ? 'Не удалось загрузить данные.' : 'Failed to load data.'}`);
        }
      } catch (err) {
        // setError('Failed to load data.');
        setError(`${lang === 'ru' ? 'Не удалось загрузить данные.' : 'Failed to load data.'}`);
        // console.error('Fetch error:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchProjects();
  }, [token]);

  if (loading) return <div>{lang === 'ru' ? 'Загрузка...' : 'Loading...'}</div>;
  if (error) return <div style={{ color: 'red' }}>{error}</div>;

  return (
    <ul>
      {treeData.map(node => (
        <TreeNode key={node.id} node={node} />
      ))}
    </ul>
  );
};

export default ProjectTree;